import { _decorator, Component, Toggle } from 'cc';
import VibrationManager from './VibrationManager';
const { ccclass, property } = _decorator;

@ccclass('VibrationToggle')
export default class VibrationToggle extends Component {
    @property(Toggle)
    private vibrationToggle: Toggle | null = null;

    start() {
        this.updateToggleState();
        if (this.vibrationToggle) {
            this.vibrationToggle.node.on('toggle', this.onToggleVibration, this);
        }
    }

    private onToggleVibration(toggle: Toggle): void {
        VibrationManager.toggleVibration();
    }

    private updateToggleState(): void {
        if (this.vibrationToggle) {
            const isVibrationOn = VibrationManager.isVibrationEnabled();
            this.vibrationToggle.isChecked = isVibrationOn;
        }
    }
}
