import { _decorator, CCInteger, Component, instantiate, Node, Prefab } from 'cc';
import { FriendItem } from './FriendItem';
import { UserDataManager } from './UserDataManager';
import { ServerCommunicator } from './ServerCommunicator';
const { ccclass, property } = _decorator;

@ccclass('FriendsScrollViewer')
export class FriendsScrollViewer extends Component {
    @property(Prefab)
    friendItemPrefab: Prefab = null;

    @property(Node)
    contentNode: Node = null;

    start() {
        this.init();
    }

    async init() {
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        const userId = UserDataManager.instance.getUserId();

        try {
            const response = await ServerCommunicator.instance.sendGetRequest('get_referrals_names?user_id=' + userId);
            
            const referralNames: string[] = response;

            if (Array.isArray(referralNames) && referralNames.every(name => typeof name === 'string')) {
                if (referralNames.length > 0) {
                    this.updateReferralList(referralNames);
                } else {
                    this.showNoReferralsMessage();
                }
            } else {
                console.error('ERROR IN GETTING FRIENDS FORMAT.');
            }
        } catch (error) {
            console.error('ERROR IN GETTING FRIENDS:', error);
        }   
    }

    updateReferralList(referralNames: string[]){
        if(this.contentNode.children.length < referralNames.length){
            if (this.contentNode.children.length > 0) {
                this.contentNode.removeAllChildren();
            }
        }

        if(this.friendItemPrefab && this.contentNode){
            for (let i = 0; i < referralNames.length; i++) {
                const newFriendItemPrefab = instantiate(this.friendItemPrefab);
                this.contentNode.addChild(newFriendItemPrefab);
                newFriendItemPrefab.getComponent(FriendItem).init(referralNames[i]);
            }
        }
    }

    showNoReferralsMessage(){
        console.log('NO FRIENDS :C');
    }
}

