import { _decorator, Component, instantiate, Node, Prefab } from 'cc';
import { UserDataManager } from './UserDataManager';
import { TaskData, TaskInfo, TasksDataManager } from './TasksDataManager';
import { TaskItem } from './TaskItem';
const { ccclass, property } = _decorator;

@ccclass('TasksScrollViewer')
export class TasksScrollViewer extends Component {
    @property(Prefab)
    taskItemPrefab: Prefab = null;

    @property(Node)
    contentNode: Node = null;

    start() {
        this.init();
    }

    async init(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        while (!TasksDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        TasksDataManager.eventTarget.on('currentUserTasksUpdated', this.updateMe, this);

        this.updateMe();
    }

    async updateMe() {
        while (!TasksDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }
    
        const userTasksInfos = await TasksDataManager.instance.getUserTasks();
        const allTasksInfos = await TasksDataManager.instance.getAllTasks();
    
        const taskInfoMap = new Map<number, TaskInfo>();
        allTasksInfos.forEach(taskInfo => {
            taskInfoMap.set(taskInfo.id, taskInfo);
        });
    
        const taskDataArray: TaskData[] = userTasksInfos.map(userTaskInfo => {
            const taskInfo = taskInfoMap.get(userTaskInfo.taskId);
            return {
                taskInfo: taskInfo!,
                userTaskInfo: userTaskInfo
            };
        });
        
        this.updateTasksList(taskDataArray);
        console.log("TASK DATA ARRAY:" + taskDataArray);
    }
    

    updateTasksList(tasks: TaskData[]){
        if (this.contentNode.children.length > 0) {
            this.contentNode.removeAllChildren();
        }

        if(this.taskItemPrefab && this.contentNode){
            for (let i = 0; i < tasks.length; i++) {
                const newFriendItemPrefab = instantiate(this.taskItemPrefab);
                this.contentNode.addChild(newFriendItemPrefab);
                newFriendItemPrefab.getComponent(TaskItem).init(tasks[i]);
            }
        }
    }
}

