import { _decorator, Component, Node } from 'cc';
import { UserDataManager } from './UserDataManager';
import { SlidePopup } from './SlidePopup';
import VibrationManager from './VibrationManager';
const { ccclass, property } = _decorator;

@ccclass('LoadingScreenHider')
export class LoadingScreenHider extends Component {

    @property(SlidePopup)
    slidePopup: SlidePopup = null;

    start() {
        this.waitForHide();
    }

    async waitForHide(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        this.hide();
    }

    hide(){
        if(this.slidePopup){
            this.slidePopup.hide();
            VibrationManager.medium();
        }
    }
}

