import { _decorator, Component, Enum, UITransform, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

enum RotationDirection {
    CLOCKWISE = 1,
    COUNTERCLOCKWISE = -1
}

@ccclass('RotateUITransform')
export class RotatableUITransform extends Component {

    @property({ type: Enum(RotationDirection), tooltip: "Pick side for rotating" })
    direction: RotationDirection = RotationDirection.CLOCKWISE;

    @property({ tooltip: "Speed of rotating" })
    rotationSpeed: number = 100;

    private _uiTransform: UITransform | null = null;

    start() {
        this._uiTransform = this.node.getComponent(UITransform);
    }

    update(deltaTime: number) {
        if (this._uiTransform) {
            const angle = this.rotationSpeed * deltaTime * this.direction;
            const currentRotation = this.node.eulerAngles;
            const newRotation = new Vec3(currentRotation.x, currentRotation.y, currentRotation.z + angle);
            this.node.eulerAngles = newRotation;
        }
    }
}
