import { _decorator, Button, Color, Component, Label, Node, Sprite } from 'cc';
import { TimerLabel } from './TimerLabel';
import { UserDataManager } from './UserDataManager';
import { ServerCommunicator } from './ServerCommunicator';
import { ButtonFeedbacks } from './ButtonFeedbacks';
const { ccclass, property } = _decorator;

@ccclass('MiningCollectController')
export class MiningCollectController extends Component {
    @property(TimerLabel)
    miningTimer: TimerLabel

    @property(Label)
    miningSpeedLabel: Label = null;

    @property(Node)
    collectMiningNode: Node = null;

    @property
    miningSpeedPostfix: String = ' sem/h'

    @property({ type: Color })
    readyForCollectNodeColor: Color = new Color(255, 255, 255);

    @property({ type: Color })
    notReadyForCollectNodeColor: Color = new Color(255, 255, 255);

    onDestroy() {
        UserDataManager.eventTarget.off('userDataUpdated', this.updateMe, this);
    }
    start() {
        UserDataManager.eventTarget.on('userDataUpdated', this.updateMe, this);
        this.init();
    }

    protected update(dt: number): void {
        if(UserDataManager.instance.isInited){
            this.updateCollectNode();
        }
    }

    async init(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        if(this.miningSpeedLabel){
            this.miningSpeedLabel.string = UserDataManager.instance.getUserCurrentSpeed().toString() + this.miningSpeedPostfix;
        }

        if(this.miningTimer){
            const startDateStr = UserDataManager.instance.getUserMiningStartDate();
            if(startDateStr && startDateStr != "null"){
                const duration = UserDataManager.instance.getUserCurrentDuration();

                this.miningTimer.init(startDateStr, duration);
            }  
        }

        if(this.collectMiningNode){
            this.collectMiningNode.on(Node.EventType.TOUCH_END, this.onClickCollectMiningNode, this);
        }
        
    }

    onClickCollectMiningNode(){
        if(!this.miningTimer.isTimerActive){
            this.tryCollectMinedToServer();
        }
    }

    async tryCollectMinedToServer(){
        const userId = UserDataManager.instance.getUserId();
        const response = await ServerCommunicator.instance.sendGetRequest('collect_mining?user_id='+userId);

        const isCollected: boolean = response;

        if(isCollected){
            UserDataManager.instance.forceUpdateUserInfo();
        }
        else{
            console.log('cant collect mined sem!')
        }
    }

    async updateMe() {
        console.log("TRYING UPDATE ME!");
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        if(this.miningSpeedLabel){
            this.miningSpeedLabel.string = UserDataManager.instance.getUserCurrentSpeed().toString() + this.miningSpeedPostfix;
        }

        if(this.miningTimer){
            const startDateStr = UserDataManager.instance.getUserMiningStartDate();
            if(startDateStr && startDateStr != "null"){
                const duration = UserDataManager.instance.getUserCurrentDuration();

                this.miningTimer.init(startDateStr, duration);
            }  
        }
    }

    updateCollectNode() {
        if(this.collectMiningNode){
            if(this.miningTimer.isTimerActive){
                this.collectMiningNode.getComponent(Sprite).color = this.notReadyForCollectNodeColor;
                this.collectMiningNode.getComponent(ButtonFeedbacks).useClickTween = false;
            }else{
                this.collectMiningNode.getComponent(Sprite).color = this.readyForCollectNodeColor;
                this.collectMiningNode.getComponent(ButtonFeedbacks).useClickTween = true;
            }
        }
    }
}

