import { _decorator, Component, Label, Node } from 'cc';
import { UserDataManager } from './UserDataManager';
const { ccclass, property } = _decorator;

@ccclass('MainMiningLabels')
export class MainMiningLabels extends Component {
    @property(Label)
    balanceLabel: Label = null;

    @property
    balancePrefix: string = 'Balance ($SEM): ';

    @property(Label)
    currentMinedLabel: Label = null;

    isMiningActive: boolean = false;
    
    endDate: Date = null;

    timeDifference: number = 0;

    protected start(): void {
        this.updateBalanceLabel();
        UserDataManager.eventTarget.on('userDataUpdated', this.updateBalanceLabel, this);
    }

    protected update(dt: number): void {
        if(UserDataManager.instance.isInited){
            this.updateCurrentMinedLabel();
        } 
    }

    onDestroy() {
        UserDataManager.eventTarget.off('userDataUpdated', this.updateBalanceLabel, this);
    }

    updateCurrentMinedLabel() {
        const startDateStr = UserDataManager.instance.getUserMiningStartDate();

        if(!startDateStr || startDateStr == "null"){
            this.currentMinedLabel.string = "00.00";
            return;
        }
        const startDate = new Date(startDateStr.replace(/-/g, "/"));

        if(!startDate){
            this.currentMinedLabel.string = "00.00";
            return;
        }
        const DurationMs = UserDataManager.instance.getUserCurrentDuration() * 60 * 60 * 1000;
    
        this.timeDifference = UserDataManager.instance.getTimeDifference();
        if (!this.timeDifference) {
            console.error("can't get sync time from userDataManager");
            return;
        }
    
        this.endDate = new Date(startDate.getTime() + DurationMs);
    
        const currentTime = new Date(new Date().getTime() + this.timeDifference);
        const timeLeft = this.endDate.getTime() - currentTime.getTime();

        if (timeLeft <= 0) {
            this.currentMinedLabel.string = (
                UserDataManager.instance.getUserCurrentSpeed() * 
                UserDataManager.instance.getUserCurrentDuration()
            ).toFixed(4);
            this.isMiningActive = false;
        } else {
            const timePassedMs = currentTime.getTime() - startDate.getTime();
            const timePassedHours = timePassedMs / (1000 * 60 * 60);
            const minedAmount = timePassedHours * UserDataManager.instance.getUserCurrentSpeed();
    
            this.currentMinedLabel.string = minedAmount.toFixed(4);
            this.isMiningActive = true;
        }
    }

    async updateBalanceLabel() {
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }
        this.balanceLabel.string = this.balancePrefix + UserDataManager.instance.getUserBalance().toFixed(2);
    }
}

