import { _decorator, Component, Node, EventTarget } from 'cc';
import { ServerCommunicator } from './ServerCommunicator';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

export interface UserInfo {
    id: string;
    name: string;
    photoUrl: string;
    balance: number;
    dateStartMining?: string;
    speedId: number;
    durationId: number;
    invitedBy?: string;
}

export interface SpeedInfo {
    id: number;
    speed: number;
    price: number;
}

export interface DurationInfo {
    id: number;
    duration: number;
    price: number;
}

@ccclass('UserDataManager')
export class UserDataManager extends Component {
    @property
    isDebug: boolean = false;

    @property
    debugId: string = "5793743593";

    @property
    debugName: string = "gerb";

    @property
    debugStartParams: string = "";
    
    private static _instance: UserDataManager;

    currentUserInfo: UserInfo = null;
    speedInfos: SpeedInfo[] = [];
    durationInfos: DurationInfo[] = [];

    isTimeSyncWithServer: boolean = false;

    timeDifference: number = null;

    firstname: string = "undefined";

    username: string = "undefined";

    defaultName: string = "Anonymous";

    public static eventTarget: EventTarget = new EventTarget();

    public static get instance(): UserDataManager {
        if (!this._instance) {
            console.error("UserDataManager instance is not initialized!");
        }
        return this._instance;
    }

    public get isInited(): boolean {
        if(this.currentUserInfo && this.speedInfos.length > 0 && this.durationInfos.length > 0 && this.isTimeSyncWithServer){
            return true;
        }
        return false;
    }
    onLoad() {
        if (UserDataManager._instance) {
            console.warn("UserDataManager instance already exists!");
            this.destroy();
            return;
        }
        UserDataManager._instance = this;
        this.syncTimeWithServer();
        
        this.setSpeedInfosFromServer();
        this.setDurationInfosFromServer();

        this.forceUpdateUserInfo();
    }

    public getUserId(): string {
        if (this.currentUserInfo) {
            return this.currentUserInfo.id;
        } else {
            console.error("Error: user information is missing");
            return null;
        }
    }

    public getUserName(): string {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.name;
    }

    public getUserPhotoUrl(): string {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.photoUrl;
    }

    public getUserBalance(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.balance;
    }

    public getUserMiningStartDate(): string {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.dateStartMining;
    }

    public getUserSpeedId(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.speedId;
    }

    public getUserDurationId(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.durationId;
    }

    public getUserInvitedBy(): string {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.currentUserInfo.invitedBy;
    }

    public getUserCurrentSpeed(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const speedInfo = this.speedInfos.find(info => info.id === this.getUserSpeedId());
        return speedInfo ? speedInfo.speed : null;
    }

    public getSpeedPriceById(id: number): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const speedInfo = this.speedInfos.find(info => info.id === id);
        return speedInfo ? speedInfo.price : null;
    }

    public getMiningSpeedById(id: number): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const speedInfo = this.speedInfos.find(info => info.id === id);
        return speedInfo ? speedInfo.speed : null;
    }

    public getSpeedInfosCount(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.speedInfos.length;
    }

    public getUserCurrentDuration(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const durationInfo = this.durationInfos.find(info => info.id === this.getUserDurationId());
        return durationInfo ? durationInfo.duration : null;
    }

    public getDurationPriceById(id: number): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const durationInfo = this.durationInfos.find(info => info.id === id);
        return durationInfo ? durationInfo.price : null;
    }

    public getMiningDurationById(id: number): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        const durationInfo = this.durationInfos.find(info => info.id === id);
        return durationInfo ? durationInfo.duration : null;
    }

    public getDurationInfosCount(): number {
        if (!this.currentUserInfo) {
            console.error("Error: user information is missing");
            return null;
        }
        return this.durationInfos.length;
    }

    async syncTimeWithServer(): Promise<void> {
        if (this.isTimeSyncWithServer) return;

        try {
            const response = await ServerCommunicator.instance.sendGetRequest('get_server_time');
            const serverTimeString: string = response;

            const serverTime = new Date(serverTimeString.replace(/-/g, "/"));

            this.timeDifference = serverTime.getTime() - new Date().getTime();
            
            this.isTimeSyncWithServer = true;
        } catch (error) {
            console.error('Error fetching server time:', error);
        }
    }

    public getTimeDifference(): number{
        if(!this.isTimeSyncWithServer){
            return null;
        }
        
        return this.timeDifference;
    }

    getGoodUserName(){
        if(this.isDebug){
            return "Debug A"
        }

        if(this.firstname === "undefined"){
            if(this.username === "undefined"){
                return this.defaultName;
            }
            return this.username;
        }
        return this.firstname;
    }

    public forceUpdateUserInfo(){
        this.setUserInfoFromServer();
    }

    async setUserInfoFromServer(){
        try{
            if(this.isDebug){
                const response = await ServerCommunicator.instance.sendGetRequest('get_user?id=' + this.debugId + '&name=' + this.debugName + '&referal=' + this.debugStartParams);
    
                const userInfoItems: any = response;
    
                console.log(userInfoItems);
    
                this.currentUserInfo = {
                    id: userInfoItems.id,
                    name: userInfoItems.username, 
                    photoUrl: userInfoItems.photo_url, 
                    balance: userInfoItems.balance,
                    dateStartMining: userInfoItems.date_start_mining,
                    speedId: userInfoItems.speed_id,
                    durationId: userInfoItems.duration_id,
                    invitedBy: userInfoItems.invited_by
                };

                UserDataManager.eventTarget.emit('userDataUpdated');
            } else {
                const tgWebApp = TelegramWebApp.Instance;
                await tgWebApp.init();
        
                const user = tgWebApp.getTelegramUser();
                const initData = tgWebApp.getTelegramWebAppInitData();

                this.firstname = user.first_name;

                this.username = user.username;
        
                const response = await ServerCommunicator.instance.sendGetRequest('get_user?id=' + user.id + '&name=' + user.username + '&referal=' + initData.start_param);

                const userInfoItems: any = response;
    
                console.log(userInfoItems);
    
                this.currentUserInfo = {
                    id: userInfoItems.id,
                    name: userInfoItems.username,
                    photoUrl: userInfoItems.photo_url,
                    balance: userInfoItems.balance,
                    dateStartMining: userInfoItems.date_start_mining,
                    speedId: userInfoItems.speed_id,
                    durationId: userInfoItems.duration_id,
                    invitedBy: userInfoItems.invited_by
                };

                UserDataManager.eventTarget.emit('userDataUpdated');
            }

            // if(!this.isInited){
            //     await new Promise(resolve => setTimeout(resolve, 500));
            //     this.forceUpdateUserInfo();
            // }
        } catch (error) {
            console.error('Error setting user:', error);
        }
    }
    

    async setSpeedInfosFromServer(){
        try {
            const response = await ServerCommunicator.instance.sendGetRequest('get_all_speeds');
            
            const allSpeeds: any[][] = response;

            this.speedInfos = [];

            this.speedInfos = allSpeeds.map((item: any) => ({
                id: item[0],
                speed: item[1],
                price: item[2]
            }));
        } catch (error) {
            console.error('Error fetching speeds:', error);
        }
    }

    async setDurationInfosFromServer(){
        try {
            const response = await ServerCommunicator.instance.sendGetRequest('get_all_durations');
            
            const allDurations: any[][] = response;

            this.durationInfos = [];

            this.durationInfos = allDurations.map((item: any) => ({
                id: item[0],
                duration: item[1],
                price: item[2]
            }));
        } catch (error) {
            console.error('Error fetching durations:', error);
        }
    }
}
