import { _decorator, color, Color, Component, Label, Node, NodeEventType, Sprite, SpriteFrame, tween, UITransform, Vec3, EventTarget, Animation } from 'cc';
import { TimerLabel } from './TimerLabel';
import { LoadingHider } from './LoadingHider';
import { UserDataManager } from './UserDataManager';
import { SuitcasesDataManager, UserSuitcaseItemInfo, SuitcaseItemInfo, UserMiningSuitcaseItemInfo } from './SuitcasesDataManager';
const { ccclass, property } = _decorator;

@ccclass('SuitcaseItemData')
class SuitcaseItemData {
    @property
    itemName: string = '';

    @property(Sprite)
    itemSprite: Sprite = null;
}

@ccclass('SuitcaseItems')
class SuitcaseItems {
    @property([SuitcaseItemData])
    itemDatas: SuitcaseItemData[] = [];
}

@ccclass('SuitcaseData')
class SuitcaseData {
    @property
    suitcaseId: number = 0;

    @property(Node)
    suitcaseNode: Node = null;

    @property(SuitcaseItems)
    items: SuitcaseItems = new SuitcaseItems();
}

@ccclass('Suitcases')
class Suitcases {
    @property([SuitcaseData])
    suitcasesDatas: SuitcaseData[] = [];
}

@ccclass('SuitcaseItemsController')
export class SuitcaseItemsController extends Component {
    @property(Suitcases)
    suitcases: Suitcases = new Suitcases();

    @property({ type: Color })
    notCollectedColor: Color = new Color(255, 255, 255);

    @property({ type: Color })
    collectedColor: Color = new Color(0, 255, 0);

    @property(Sprite)
    currentMiningItemSprite: Sprite = null;

    @property(TimerLabel)
    miningItemTimerLabel: TimerLabel = null;

    @property(LoadingHider)
    loadingHiderSuitcase: LoadingHider = null;

    @property(LoadingHider)
    loadingHiderCurrentItem: LoadingHider = null;

    @property(Node)
    allSuitcasesPackedNode: Node = null;

    _currentMiningItem: SuitcaseItemInfo;
    _currentUserMiningSuitcaseItemInfo: UserMiningSuitcaseItemInfo;

    public eventTarget: EventTarget = new EventTarget();

    protected start(): void {
        this.init();
    }

    async init() {
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        while (!SuitcasesDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        await this.setCurrentSuitcase();
        await this.setItemsColors();

        this.currentMiningItemSprite.node.on(Node.EventType.TOUCH_END, this.onClickCurrentMiningItemSprite, this);
    }

    async onClickCurrentMiningItemSprite() {
        if (!this._currentMiningItem && !this._currentUserMiningSuitcaseItemInfo) {
            console.log('Nothing In Mining');
            return;
        }

        if (!this.miningItemTimerLabel.isTimerActive) {
            this.tryCollectCurrentMiningItem();
        }
    }

    protected update(dt: number): void {
        if(SuitcasesDataManager.instance.isInited){
            if (this.currentMiningItemSprite) {
                if (!this.miningItemTimerLabel.isTimerActive) {
                    this.currentMiningItemSprite.grayscale = false;
                    this.currentMiningItemSprite.node.getComponent(Animation).enabled = true;
                }
                else{
                    this.currentMiningItemSprite.grayscale = true;
                    this.currentMiningItemSprite.node.getComponent(Animation).enabled = false;
                }
            }
        }
    }

    async tryCollectCurrentMiningItem() {
        await SuitcasesDataManager.instance.collectMinedItem();
        await this.setCurrentSuitcase(true);
        await this.setItemsColors(true);
    }

    async setCurrentSuitcase(withForceUpdate: boolean = false) {
        if (this.loadingHiderSuitcase) {
            this.loadingHiderSuitcase.show();
        }

        if (this.loadingHiderCurrentItem) {
            //this.loadingHiderCurrentItem.show();
        }

        if (this.suitcases && this.suitcases.suitcasesDatas.length > 0) {
            this.suitcases.suitcasesDatas.forEach(function (value) {
                if (value.suitcaseNode) {
                    value.suitcaseNode.active = false;
                } else {
                    console.error("suitcaseNode not inited with ID: " + value.suitcaseId);
                }
            });
        } else {
            console.error("Array suitcasesDatas empty.");
        }

        const currentSuitcaseIndex = await SuitcasesDataManager.instance.getCurrentSuitcaseIndex(withForceUpdate);

        if (currentSuitcaseIndex === null || currentSuitcaseIndex === undefined || !Number.isInteger(currentSuitcaseIndex)) {
            console.log("ALL SUITCASES FILLED!")
            if (this.allSuitcasesPackedNode) {
                this.allSuitcasesPackedNode.active = true;
            }
            if (this.loadingHiderCurrentItem) {
                //this.loadingHiderCurrentItem.node.active = false;
            }
            return;
        }

        if (currentSuitcaseIndex !== null && currentSuitcaseIndex >= 0 && this.suitcases.suitcasesDatas[currentSuitcaseIndex]) {
            if (this.suitcases.suitcasesDatas[currentSuitcaseIndex].suitcaseNode) {
                this.suitcases.suitcasesDatas[currentSuitcaseIndex].suitcaseNode.active = true;
                console.log('Suitcase inited');
            } else {
                console.error("suitcaseNode not inited with ID: " + this.suitcases.suitcasesDatas[currentSuitcaseIndex].suitcaseId);
            }
        } else {
            console.log('Not founded');
        }
    }

    async setItemsColors(withForceUpdate: boolean = false) {
        const currentSuitcaseIndex = await SuitcasesDataManager.instance.getCurrentSuitcaseIndex(withForceUpdate);

        if (currentSuitcaseIndex === null || currentSuitcaseIndex === undefined || !Number.isInteger(currentSuitcaseIndex)) {
            console.log("ALL SUITCASES FILLED!")
            if (this.allSuitcasesPackedNode) {
                this.allSuitcasesPackedNode.active = true;
            }
            if (this.loadingHiderCurrentItem) {
                //this.loadingHiderCurrentItem.node.active = false;
            }
            return;
        }

        if (currentSuitcaseIndex !== null && currentSuitcaseIndex >= 0) {
            this.suitcases.suitcasesDatas[currentSuitcaseIndex].items.itemDatas.forEach((value) => {
                if (value.itemSprite) {
                    value.itemSprite.color = this.notCollectedColor;
                } else {
                    console.error("itemSprite not inited for: " + value.itemName);
                }
            });
        } else {
            console.log("Not founded");
        }

        const allItems = await SuitcasesDataManager.instance.getAllItems(withForceUpdate);
        const currentUserSuitcaseItems = await SuitcasesDataManager.instance.getCurrentUserSuitcaseItems(withForceUpdate);
        const currentMiningUserItems = await SuitcasesDataManager.instance.getCurrentMiningUserSuitcaseItems(withForceUpdate);

        for (const value of this.suitcases.suitcasesDatas[currentSuitcaseIndex].items.itemDatas) {
            const suitcaseItemDataName = value.itemName;
            if (!suitcaseItemDataName) {
                console.error('Not founded suitcaseItemDataName');
                return;
            }
            if (allItems.length === 0) return;

            const suitcaseItemInfo = allItems.find(item => item.name === suitcaseItemDataName);
            if (!suitcaseItemInfo) {
                console.warn('Not founded suitcaseItemInfo');
                continue;
            }

            if (currentUserSuitcaseItems.length === 0) return;

            const suitcaseUserItemInfo = currentUserSuitcaseItems.find(item => item.itemId === suitcaseItemInfo.id);
            if (!suitcaseUserItemInfo) {
                console.error('Not founded suitcaseUserItemInfo');
                return;
            }

            if (currentMiningUserItems.length === 0) return;

            const currentUserMiningItem = currentMiningUserItems.find(item => item.itemId === suitcaseItemInfo.id);
            if (!currentUserMiningItem) {
                console.log('Not founded currentUserMiningItem');
                continue;
            }

            if (suitcaseUserItemInfo.status == 'collected') {
                value.itemSprite.color = this.collectedColor;
            } else if (suitcaseUserItemInfo.status == 'mining') {
                this.updateCurrentMiningItem(value.itemSprite.spriteFrame, suitcaseItemInfo, currentUserMiningItem);
                this._currentUserMiningSuitcaseItemInfo = currentUserMiningItem;
                this._currentMiningItem = suitcaseItemInfo;
            }

            if (this.loadingHiderSuitcase) {
                this.loadingHiderSuitcase.hide();
            }
        }
    }

    updateCurrentMiningItem(spriteFrame: SpriteFrame, suitcaseItemInfo: SuitcaseItemInfo, userMiningItem: UserMiningSuitcaseItemInfo) {
        if (!spriteFrame || !suitcaseItemInfo || !userMiningItem) {
            console.error('Not found spriteFrame or suitcaseItemInfo or userMiningItem');
            return;
        }

        if (this.miningItemTimerLabel) {
            this.miningItemTimerLabel.init(userMiningItem.dataStartMiningItem, suitcaseItemInfo.collectionDuration);
        }

        if (this.currentMiningItemSprite) {
            if (!this.miningItemTimerLabel.isTimerActive) {
                this.currentMiningItemSprite.grayscale = false;
                this.currentMiningItemSprite.node.getComponent(Animation).enabled = true;
            }
            else{
                this.currentMiningItemSprite.grayscale = true;
                this.currentMiningItemSprite.node.getComponent(Animation).enabled = false;
            }
        }

        if (this.loadingHiderCurrentItem) {
            //this.loadingHiderCurrentItem.hide();
        }
    }
}
