import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ServerCommunicator')
export class ServerCommunicator extends Component {
    private static _instance: ServerCommunicator;

    public static get instance(): ServerCommunicator {
        if (!this._instance) {
            console.error("ServerCommunicator instance is not initialized!");
        }
        return this._instance;
    }

    onLoad() {
        if (ServerCommunicator._instance) {
            console.warn("ServerCommunicator instance already exists!");
            this.destroy();
            return;
        }
        ServerCommunicator._instance = this;
    }

    public async sendGetRequest(endpoint: string): Promise<any> {
        const url = `https://hmshtlser.online/${endpoint}`;

        const response = await fetch(url, {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        });
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        return await response.json();
    }

    public async sendPostRequest(endpoint: string): Promise<any> {
        const url = `https://hmshtlser.online/${endpoint}`;
    
        const response = await fetch(url, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            }
        });
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        return await response.json();
    }
}
