import { _decorator, Component, Label, } from 'cc';
import { SuitcasesDataManager } from './SuitcasesDataManager';
const { ccclass, property } = _decorator;

@ccclass('SuitcaseCounter')
export class SuitcaseCounter extends Component {
    @property(Label)
    myLabel: Label = null;


    start() {
        this.initWithSuitcaseCount();
    }

    async initWithSuitcaseCount(){
            while (!SuitcasesDataManager.instance.isInited) {
                await new Promise(resolve => setTimeout(resolve, 100));
            }

            SuitcasesDataManager.eventTarget.on('currentSuitcasesUpdated', this.updateMe, this);
        
            if(this.myLabel){
                this.myLabel.string = "x"+(await SuitcasesDataManager.instance.getCurrentSuitcaseFilledCount()).toString();
            }
        
    }
    
    async updateMe(){
        if(this.myLabel){
            this.myLabel.string = "x"+(await SuitcasesDataManager.instance.getCurrentSuitcaseFilledCount()).toString();
        }
    }
}

