import { _decorator, Component, Node, UITransform } from 'cc';
import { SlidePopup } from './SlidePopup';
const { ccclass, property } = _decorator;

@ccclass('PopupManager')
export class PopupManager extends Component {
    private static instance: PopupManager;

    @property([Node])
    popups: Node[] = [];

    onLoad() {
        if (PopupManager.instance) {
            this.destroy();
            return;
        }
        PopupManager.instance = this;
        this.node.addComponent(UITransform);
    }

    public static getInstance(): PopupManager {
        return PopupManager.instance;
    }

    showPopup(index: number) {
        if (index < 0 || index >= this.popups.length) {
            console.warn('Popup index out of bounds');
            return;
        }

        const popupComponent = this.popups[index].getComponent(SlidePopup);
        if (popupComponent) {
            popupComponent.show();
        } else {
            console.warn('SlidePopup component not found on node:', this.popups[index].name);
        }
    }

    hidePopup(index: number) {
        if (index < 0 || index >= this.popups.length) {
            console.warn('Popup index out of bounds');
            return;
        }

        const popupComponent = this.popups[index].getComponent(SlidePopup);
        if (popupComponent) {
            popupComponent.hide();
        } else {
            console.warn('SlidePopup component not found on node:', this.popups[index].name);
        }
    }

    switchPopup(currentIndex: number, nextIndex: number) {
        this.hidePopup(currentIndex);
        this.showPopup(nextIndex);
    }
}
