import { _decorator, Component, Label, Node } from 'cc';
import { UserDataManager } from './UserDataManager';
const { ccclass, property } = _decorator;

@ccclass('BalanceLabel')
export class BalanceLabel extends Component {
    @property(Label)
    myLabel: Label = null;

    start() {
        this.initWithUserData();
    }

    async initWithUserData(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        UserDataManager.eventTarget.on('userDataUpdated', this.updateMe, this);
        
        if(this.myLabel){
            this.myLabel.string = UserDataManager.instance.getUserBalance().toFixed(2);
        }
    }
    
    updateMe(){
        if(this.myLabel){
            this.myLabel.string = UserDataManager.instance.getUserBalance().toFixed(2);
        }
    }
}

