import { _decorator, color, Color, Component, EventHandler, Mask, Node, Sprite, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('LoadingHider')
export class LoadingHider extends Component {

    @property(Node)
    myNode: Node = null;

    @property
    hideDuration: number = 0.5;

    @property
    disactivateOnHide: boolean = false;

    @property({ type: Color })
    shownColor: Color = new Color(255, 255, 255);

    @property({ type: Color })
    hidenColor: Color = new Color(255, 255, 255, 0);

    @property(Mask)
    maskToUse: Mask = null;
    

    show() {
        if(this.myNode){
            this.myNode!.active = true;
            const sprite = this.myNode.getComponent(Sprite);
            if (!sprite) return;

            if(this.maskToUse){
                this.maskToUse.enabled = true;
            }

            sprite.color = this.shownColor;
        }
    }

    hide(){
        if(this.myNode){
            const sprite = this.myNode.getComponent(Sprite);
            if (!sprite) return;

            if(this.maskToUse){
                this.maskToUse.enabled = false;
            }

            const initialColor = sprite.color.clone();
            const targetColor = this.hidenColor;

            tween(sprite)
                .to(this.hideDuration, { color: targetColor }, {
                    onUpdate: () => {
                        sprite.color = new Color(initialColor.r, initialColor.g, initialColor.b, sprite.color.a);
                    }
                })
                .call(() => {
                    if(this.disactivateOnHide){
                        this.myNode!.active = false;
                    }
                })
                .start();
        }
    }
}

