import { _decorator, Component, Label } from 'cc';
import { ServerCommunicator } from './ServerCommunicator';
import { UserDataManager } from './UserDataManager';
const { ccclass, property } = _decorator;

@ccclass('TimerLabel')
export class TimerLabel extends Component {
    @property(Label)
    myLabel: Label = null;

    private endDate: Date = null;
    public isTimerActive: boolean = false;

    private timeDifference: number = 0;

    protected start(): void {
        this.myLabel.string = '00:00:00:00';
    }

    async init(dateString: string, duration: number): Promise<void> {

        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        const startDate = new Date(dateString.replace(/-/g, "/"));
        const DurationMs = duration * 60 * 60 * 1000;

        this.timeDifference = UserDataManager.instance.getTimeDifference();
        if(!this.timeDifference){
            console.error("cant get sync time from userDataManager");
            return;
        }

        this.endDate = new Date(startDate.getTime() + DurationMs);
        this.isTimerActive = true;

        this.updateLabel();
    }

    protected update(dt: number): void {
        if (this.isTimerActive && this.endDate) {
            this.updateLabel();
        }
    }

    private updateLabel(): void {
        const currentTime = new Date(new Date().getTime() + this.timeDifference);
        const timeLeft = this.endDate.getTime() - currentTime.getTime();

        if (timeLeft <= 0) {
            this.myLabel.string = "00:00:00:00";
            this.isTimerActive = false;
        } else {
            this.myLabel.string = this.formatTimeLeft(timeLeft);
        }
    }

    formatTimeLeft(timeLeft: number): string {
        const seconds = Math.floor((timeLeft / 1000) % 60);
        const minutes = Math.floor((timeLeft / (1000 * 60)) % 60);
        const hours = Math.floor((timeLeft / (1000 * 60 * 60)) % 24);
        const days = Math.floor(timeLeft / (1000 * 60 * 60 * 24));

        function padZero(num: number): string {
            return (num < 10 ? '0' : '') + num;
        }

        return padZero(days) + ':' + padZero(hours) + ':' + padZero(minutes) + ':' + padZero(seconds);
    }
}
