import { _decorator, Component, Label, Node } from 'cc';
import { UserDataManager } from './UserDataManager';
import { BoostItem } from './BoostItem';
import { BoostType } from './BoostType';
import { ServerCommunicator } from './ServerCommunicator';
const { ccclass, property } = _decorator;

@ccclass('BoostsController')
export class BoostsController extends Component {
    @property(BoostItem)
    durationBoost: BoostItem = null;

    @property(BoostItem)
    speedBoost: BoostItem = null;

    protected start(): void {
        this.loadDataFromUserData();
        UserDataManager.eventTarget.on('userDataUpdated', this.loadDataFromUserData, this);
    }

    async loadDataFromUserData(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }
        this.setDurationBoostInfo();
        this.setSpeedBoostInfo();
    }

    setSpeedBoostInfo(){
        const curMiningSpeedId = UserDataManager.instance.getUserSpeedId();
        const curMiningSpeed = UserDataManager.instance.getUserCurrentSpeed();
        const nextMiningSpeedId =  curMiningSpeedId + 1;

        if(nextMiningSpeedId > UserDataManager.instance.getSpeedInfosCount()){
            console.log("NO MORE MINING SPEEDS!")
            this.speedBoost.updateMeLikeFull(curMiningSpeedId, curMiningSpeed);
            return;
        }

        const nextMiningSpeed = UserDataManager.instance.getMiningSpeedById(nextMiningSpeedId);
        const nextMiningSpeedPrice = UserDataManager.instance.getSpeedPriceById(nextMiningSpeedId);

        this.speedBoost.updateMe(curMiningSpeedId, curMiningSpeed, nextMiningSpeed, nextMiningSpeedPrice);
    }

    setDurationBoostInfo(){
        const curMiningDurationId = UserDataManager.instance.getUserDurationId();
        const curMiningDuration = UserDataManager.instance.getUserCurrentDuration();
        const nextMiningDurationId =  curMiningDurationId + 1;

        if(nextMiningDurationId > UserDataManager.instance.getDurationInfosCount()){
            console.log("NO MORE MINING DURATIONS!")
            this.durationBoost.updateMeLikeFull(curMiningDurationId, curMiningDuration);
            return;
        }
        
        const nextMiningDuration = UserDataManager.instance.getMiningDurationById(nextMiningDurationId);
        const nextMiningDurationPrice = UserDataManager.instance.getDurationPriceById(nextMiningDurationId);

        this.durationBoost.updateMe(curMiningDurationId, curMiningDuration, nextMiningDuration, nextMiningDurationPrice);
    }

    buyBoostByBoostType(boostType: BoostType){
        if(boostType == BoostType.Duration){
            this.tryBuyNextDuration();
        }
        if(boostType == BoostType.Speed){
            this.tryBuyNextSpeed();
        }
    }

    async tryBuyNextDuration(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        this.durationBoost.tryingPurchase();

        const userId = UserDataManager.instance.getUserId();
        const response = await ServerCommunicator.instance.sendGetRequest('boost_duration?user_id='+userId);
        
        const succBuy: any = response[0]

        if(succBuy == true){
            this.durationBoost.purchaseSuccessful();
        }
        else{
            this.durationBoost.purchaseUnsuccessful();
        }

        UserDataManager.instance.forceUpdateUserInfo();
    }

    async tryBuyNextSpeed(){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        this.speedBoost.tryingPurchase();

        const userId = UserDataManager.instance.getUserId();
        const response = await ServerCommunicator.instance.sendGetRequest('boost_speed?user_id='+userId);

        const succBuy: any = response[0]

        if(succBuy == true){
            this.speedBoost.purchaseSuccessful();
        }
        else{
            this.speedBoost.purchaseUnsuccessful();
        }

        UserDataManager.instance.forceUpdateUserInfo();
    }
}

