import { _decorator, Color, Component, Enum, Label, Node, RichText, Sprite } from 'cc';
import { LoadingHider } from './LoadingHider';
import { BoostsController } from './BoostsController';
import { BoostType } from './BoostType';
import VibrationManager from './VibrationManager';
import { UserDataManager } from './UserDataManager';
const { ccclass, property } = _decorator;

@ccclass('BoostItem')
export class BoostItem extends Component {
    @property(BoostsController)
    boostController: BoostsController = null

    @property({ type: Enum(BoostType) })
    myBoostType: BoostType = BoostType.Duration;

    @property(Label)
    levelLabel: Label = null;

    @property
    levelPrefix: string = "Level ";

    @property(RichText)
    valuesRichText: RichText = null;

    @property
    valuesPostfix: string = "h";

    @property(Color)
    currentValueTextColor: Color = new Color(255, 255, 255);

    @property(Color)
    nextValueTextColor: Color = new Color(255, 255, 255);

    @property(Label)
    priceLabel: Label = null;

    @property
    pricePrefix: string = "Price: ";

    @property
    pricePostfix: string = " $SEM";

    @property(Node)
    buyButtonNode: Node = null;

    @property(Color)
    activeButtonColor: Color = new Color(255, 255, 255);

    @property(Color)
    unactiveButtonColor: Color = new Color(255, 255, 255);

    @property(LoadingHider)
    loader: LoadingHider = null;

    isButtonActive = false;

    protected onLoad(): void {
        if(this.loader){
            this.loader.show();
        }

        if(this.buyButtonNode){
            this.buyButtonNode.on(Node.EventType.TOUCH_END, this.buyButtonPressed, this);
        }
    }

    private colorToHex(color: Color): string {
        const r = this.componentToHex(color.r);
        const g = this.componentToHex(color.g);
        const b = this.componentToHex(color.b);
        return `${r}${g}${b}`;
    }

    private componentToHex(c: number): string {
        const hex = c.toString(16);
        return hex.length == 1 ? '0' + hex : hex;
    }

    updateMe(level: number, currentValue: number, nextValue: number, priceForNext: number) {
        this.levelLabel.string = this.levelPrefix + level.toString();

        const currentColorHex = this.colorToHex(this.currentValueTextColor);
        const nextColorHex = this.colorToHex(this.nextValueTextColor);

        this.valuesRichText.string = `<color=#${currentColorHex}>${currentValue}${this.valuesPostfix} > </color><color=#${nextColorHex}>${nextValue}${this.valuesPostfix}</color>`;

        this.priceLabel.string = this.pricePrefix + priceForNext.toString() + this.pricePostfix;

        const balance = UserDataManager.instance.getUserBalance();

        if(balance >= priceForNext){
            this.buyButtonActivity(true);
        }else{
            this.buyButtonActivity(false);
        }
        
        if(this.loader){
            this.loader.hide();
        }
    }

    updateMeLikeFull(level: number, currentValue: number) {
        this.levelLabel.string = this.levelPrefix + level.toString();

        const currentColorHex = this.colorToHex(this.currentValueTextColor);

        this.valuesRichText.string = `<color=#${currentColorHex}>${currentValue}${this.valuesPostfix}</color>`;

        this.priceLabel.string = "Maximum boost!";
        this.buyButtonActivity(false);
        if(this.loader){
            this.loader.hide();
        }
    }

    buyButtonActivity(actvivty: boolean){
        if(this.buyButtonNode){
            if(actvivty){
                this.buyButtonNode.getComponent(Sprite).color = this.activeButtonColor;
                this.isButtonActive = true;
            }
            else{
                this.buyButtonNode.getComponent(Sprite).color = this.unactiveButtonColor;
                this.isButtonActive = false;
            }
        }
    }

    buyButtonPressed(){
        if(this.isButtonActive){
            this.buyButtonActivity(false);
            this.boostController.buyBoostByBoostType(this.myBoostType);
        }
    }

    tryingPurchase(){
        if(this.loader){
            this.loader.show();
        }
    }

    purchaseSuccessful(){
        VibrationManager.success();
    }

    purchaseUnsuccessful(){
        VibrationManager.error();
    }
}