import { _decorator, Component, Label, Node } from 'cc';
import { ServerCommunicator } from "./ServerCommunicator";
const { ccclass, property } = _decorator;

export interface SuitcaseItemInfo {
    id: number;
    name: string;
    collectionDuration: number;
    price: number;
    chance: number;
    suitcaseTypeId: number;
}

@ccclass('TestGetLabel')
export class TestGetLabel extends Component {
    @property(ServerCommunicator)
    serverCommunicator: ServerCommunicator=null;
    @property(Label)
    label: Label = null;

    currentAllItems: SuitcaseItemInfo[] = [];

    protected start(): void {
        this.getMetod();
    }

    async getMetod(){
        try {
            const response = await this.serverCommunicator.sendGetRequest('get_all_items');

            const allItems: any[][] = response;

            this.currentAllItems = allItems.map((item: any) => ({
                id: item[0],
                name: item[1],
                collectionDuration: item[2],
                price: item[3],
                chance: item[4],
                suitcaseTypeId: item[5]
            }));
        } catch (error) {
            console.error('Error fetching items:', error);
        }

        
        this.label.string = this.currentAllItems[1].name;
    }
}

