import { _decorator, Component, EventTarget } from 'cc';
import { ServerCommunicator } from './ServerCommunicator';
import { UserDataManager } from './UserDataManager';
const { ccclass } = _decorator;

export enum TaskType {
    Other = "Other",
    GoTo = "GoTo",
    Subscribe = "Subscribe",
    MoneyCount = "MoneyCount",
    FriendsCount = "FriendsCount",
}

export interface UserTaskInfo {
    id: number;
    userId: string;
    taskId: number;
    status: string;
}

export interface TaskInfo {
    id: number;
    name: string;
    reward: number;
    taskType: TaskType;
    details: string;
}

export interface TaskData{
    taskInfo: TaskInfo;
    userTaskInfo: UserTaskInfo;
}

@ccclass('TasksDataManager')
export class TasksDataManager extends Component {
    private static _instance: TasksDataManager;

    public static get instance(): TasksDataManager {
        if (!this._instance) {
            console.error("TasksDataManager instance is not initialized!");
        }
        return this._instance;
    }

    public static eventTarget: EventTarget = new EventTarget();

    currentTasks: TaskInfo[] = [];
    currentUserTasks: UserTaskInfo[] = [];

    onLoad() {
        if (TasksDataManager._instance) {
            console.warn("TasksDataManager instance already exists!");
            this.destroy();
            return;
        }
        TasksDataManager._instance = this;

        this.forceUpdateData();
    }

    public get isInited(): boolean {
        return this.currentTasks.length > 0 && this.currentUserTasks.length > 0;
    }

    public async forceUpdateData() {
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        await this.setAllTasksFromServer();
        await this.setCurrentUserTasksFromServer();
    }

    async setAllTasksFromServer() {
        try {
            const response = await ServerCommunicator.instance.sendGetRequest('get_all_tasks');
            const allTasks: any[][] = response;

            this.currentTasks = allTasks.map((item: any) => ({
                id: item[0],
                name: item[1],
                reward: item[2],
                taskType: this.mapTaskType(item[3]),
                details: item[4]
            }));

            TasksDataManager.eventTarget.emit('allTasksUpdated');
        } catch (error) {
            console.error('Error fetching tasks:', error);
        }
    }

    async setCurrentUserTasksFromServer() {
        try {
            const userId = UserDataManager.instance.getUserId();
            const response = await ServerCommunicator.instance.sendGetRequest('get_user_tasks?user_id=' + userId);

            const userTasks: any[][] = response;

            if (!Array.isArray(userTasks)) {
                return;
            }

            this.currentUserTasks = userTasks.map((item: any) => ({
                id: item[0],
                userId: item[1],
                taskId: item[2],
                status: item[3]
            }));

            TasksDataManager.eventTarget.emit('currentUserTasksUpdated');
        } catch (error) {
            console.error('Error fetching user tasks:', error);
        }
    }

    async checkTaskCompleted(taskId: number){
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        const userId = UserDataManager.instance.getUserId();
        const response = await ServerCommunicator.instance.sendGetRequest('check_task_completion?user_id='+userId+"&task_id="+taskId);

        const taskCompleted: boolean = response[0]

        return taskCompleted;
    }

    public async getAllTasks(forceUpdate: boolean = false): Promise<TaskInfo[]> {
        if (this.currentTasks.length <= 0 || forceUpdate) {
            await this.setAllTasksFromServer();
        }
        return this.currentTasks;
    }

    public async getUserTasks(forceUpdate: boolean = false): Promise<UserTaskInfo[]> {
        if (this.currentUserTasks.length <= 0 || forceUpdate) {
            await this.setCurrentUserTasksFromServer();
        }
        return this.currentUserTasks;
    }

    private mapTaskType(taskTypeString: string): TaskType {
        switch (taskTypeString) {
            case "GoTo":
                return TaskType.GoTo;
            case "Subscribe":
                return TaskType.Subscribe;
            case "MoneyCount":
                return TaskType.MoneyCount;
            case "FriendsCount":
                return TaskType.FriendsCount;
            case "Other":
            default:
                return TaskType.Other;
        }
    }
}
